<template>
    <div class="ratings" ref="ratingDetail">
        <div>
            <div>
                <div class="head">
                    <div class="left">
                        <p class="rating">3.9</p>
                        <p class="comprehensive">综合评分</p>
                        <p class="better">高于周边商家69.2%</p>
                    </div>
                    <div class="right">
                        <div class="right1">
                            <span class="attitude">服务态度</span>
                            <star :score='seller.score' :size="'small'" class="star"></star>
                            <span class="score">3.9</span>
                        </div>
                        <div class="right2">
                            <span class="attitude">服务态度</span>
                            <star :score='seller.score' :size="'small'" class="star"></star>
                            <span class="score">4.0</span>
                        </div>
                        <div class="right3">
                            <span class="attitude">送达时间</span>
                            <span class="score">44分钟</span>
                        </div>
                    </div>
                </div>
                <div class="middle"></div>
                <div class="foot">
                    <div class="list">
                        <p class="total" @click="select(2)">全部&nbsp;&nbsp;<span class="num">{{ratings.length}}</span></p>
                        <p class="Recommend" @click="select(0)">满意&nbsp;&nbsp;<span class="num">{{goodCount}}</span></p>
                        <p class="complaints" @click="select(1)">不满意&nbsp;&nbsp;<span class="num">{{ratings.length-goodCount}}</span></p>
                    </div>
                    <div class="content">
                        <i class="iconfont yes" @click="toggle" :class="{'toggle' : onlyContent!==false}">&#xe608;</i>
                        <span class="watchContent">只看有内容的评价</span>
                    </div>
                     <div v-for="(item,i) in selectRatings" :key="i" class="details">
                        <div class="detail">
                            <img :src="item.avatar" alt="" class="headImg">
                            <div class="user">
                                <div class="userList">
                                    <p class="username">{{item.username}}</p>
                                    <p class="rateTime">{{item.rateTime |change}}</p>
                                </div>
                                <div class="userStar">
                                    <star :score='item.score' :size="'small'" class="star"></star>
                                    <p v-if="item.deliveryTime" class="deliveryTime">{{item.deliveryTime}}分钟送达</p>
                                </div>
                                <p class="text" v-if="item.text">{{item.text}}</p>
                                <i class="iconfont" :class="item.rateType === 0 ? 'icon-damuzhion':'icon-down'"></i>
                                <ul v-if="item.recommend.length" class="recommend">
                                    <li>{{item.recommend[0]}}</li>
                                    <li v-if="item.recommend[1]">{{item.recommend[1]}}</li>
                                    <li v-if="item.recommend[2]">{{item.recommend[2]}}</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>  
        </div>
    </div>
</template>

<script>
    import star from '../star/star.vue'
    import { mapGetters,mapActions } from 'vuex';
    import BScroll from 'better-scroll';
    export default {
        components:{
            star
        },
        computed:{
            ...mapGetters([
                'seller',
                'ratings',
                'selectRatings',
                'onlyContent'
            ]),
            goodCount(){
                return this.ratings.filter(rating=>{
                    return rating.rateType===0;
                }).length;
            },
        },
        watch:{
            selectRatings(){
                this.$nextTick( () => {
				    setTimeout( () => {
                        if( !this.ratingBScroll){
                            this.ratingBScroll = new BScroll(this.$refs.ratingDetail,{click:true})
                        }else{
                            this.ratingBScroll.refresh();
                        }
				    },30);
			    })
            }
        },
        filters:{
            change(value){
                let date=new Date(value);
                let Y=date.getFullYear()+'-';
                let M=(date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
                let D=date.getDate() + ' ';
                let h = date.getHours() + ':';
                let m = date.getMinutes() + ':';
                let s = date.getSeconds(); 
                return value=Y+M+D+h+m+s ;                                                                                                                                             
            }
        },
        created(){
            this.$store.dispatch('showRatings');
        },
        methods:{
            select(n){
                this.$store.dispatch('select',n);
            },
            toggle(){
                this.$store.dispatch('toggle');
            }
        }
    }
</script>

<style lang="less" scoped>
    .ratings{
        position: absolute;
        top: 3.49rem;
        overflow: hidden;
        width:100%;
        bottom: 1rem;
        .head{
            width: 100%;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            .left{
                width: 2.75rem;
                height:1.4rem;
                margin:0.36rem 0;
                border-right: 0.01rem solid rgba(7,17,27,0.1);
                text-align: center;
                .rating{
                    font-size: 0.48rem;
                    color:rgb(255,153,0);
                    line-height: 0.56rem;
                    
                }
                .comprehensive{
                    margin-top: 0.12rem;
                    font-size: 0.24rem;
                    color:rgb(7,17,27);
                    line-height: 0.24rem;
                }
                .better{
                    margin-top: 0.16rem;
                    font-size: 0.2rem;
                    color:rgb(7,17,27);
                    line-height: 0.2rem;
                }
            }
            .right{
                position: absolute;
                left: 3.23rem;
                top:0rem;
                .right1{
                    display: flex;
                    justify-content:space-between;
                    .attitude{
                        font-size: 0.24rem;
                        color:rgb(7,17,27);
                        line-height: 0.36rem;
                    }
                    .star{
                        padding-left: 0.24rem;
                        padding-top: 0.05rem;
                    }
                    .score{
                        padding-left: 0.24rem;
                        font-size: 0.24rem;
                        color:rgb(255,153,0);
                        line-height: 0.36rem;
                    }
                }
                .right2{
                    margin-top: 0.16rem;
                    display: flex;
                    justify-content:space-between;
                    .attitude{
                        font-size: 0.24rem;
                        color:rgb(7,17,27);
                        line-height: 0.36rem;
                    }
                    .star{
                        padding-left: 0.24rem;
                        padding-top: 0.05rem;
                    }
                    .score{
                        padding-left: 0.24rem;
                        font-size: 0.24rem;
                        color:rgb(255,153,0);
                        line-height: 0.36rem;
                    }
                }
                .right3{
                    margin-top: 0.16rem;
                    display: flex;
                    justify-content:space-between;
                    .attitude{
                        font-size: 0.24rem;
                        color:rgb(7,17,27);
                        line-height: 0.36rem;
                    }
                    .score{
                        padding-left: 0.24rem;
                        font-size: 0.24rem;
                        color:rgb(147,153,159);
                        line-height: 0.36rem;
                        position: relative;
                        left:-1.3rem;
                    }
                }
            }
        }
        .middle{
            width: 100%;
            height: 0.32rem;
            background-color: #f3f5f7;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
        }
        .foot{
            width: 100%;
            .list{
                margin:0.36rem 0.36rem 0 0.36rem;
                padding-bottom: 0.36rem;
                display: flex;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                .total{ 
                    width: 1.19rem;
                    height: 0.64rem;
                    background-color: rgb(0,160,220);
                    color:rgb(255,255,255);
                    font-size: 0.24rem;
                    line-height: 0.64rem;
                    padding-left: 0.24rem;
                    border-radius:0.02rem;
                    .num{
                        font-size: 0.24rem;
                    }
                }
                .Recommend{
                    width: 1.19rem;
                    height: 0.64rem;
                    background-color: rgba(0,160,220,0.2);
                    border-radius:0.02rem;
                    margin-left: 0.16rem;
                    padding-left: 0.24rem;
                    font-size: 0.24rem;
                    color:rgb(77,85,93);
                    line-height: 0.64rem;
                }
                .complaints{
                    width: 1.19rem;
                    height: 0.64rem;
                    background-color: rgba(77,85,93,0.2);
                    border-radius:0.02rem;
                    margin-left: 0.16rem;
                    padding-left: 0.24rem;
                    font-size: 0.24rem;
                    color:rgb(77,85,93);
                    line-height: 0.64rem;
                    .num{
                        position: relative;
                        right:0.04rem;
                    }
                }
            }
            .content{
                width: 100%;
                height: 1rem;
                margin:0.24rem 0 0 0;
                padding-left: 0.36rem;
                border-bottom:0.01rem solid rgba(7,17,27,0.1);
                .yes{
                    color:rgb(147,153,159);
                    // color:rgb(0,160,220);
                    &.toggle{
                        color:rgb(0,160,220);
                    }
                }
                .watchContent{
                    padding-left: 0.08rem;
                    font-size: 0.24rem;
                    color:rgb(147,153,159);
                }
            }
            .details{
                width:100%;
                display: flex;
                .detail{
                    width: 90%;
                    margin:0 0.36rem;
                    padding: 0.36rem 0;
                    border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                    .headImg{
                        width: 0.56rem;
                        height: 0.56rem;
                        border-radius: 50%;
                        float: left;
                    }
                    .user{
                        float: left;
                        width: 85%;
                        margin-left: 0.24rem;
                        .userList{
                            display: flex;
                            justify-content:space-between;
                            .username{
                                font-size: 0.2rem;
                                color:rgb(7,17,27);
                                line-height: 0.24rem;
                            }
                            .rateTime{
                                font-size: 0.2rem;
                                color:rgb(147,153,159);
                                line-height: 0.24rem;
                                font-weight: 200;
                            }
                        }
                        .userStar{
                            width: 85%;
                            height: 0.25rem;
                            margin-top: 0.08rem;
                            .star{
                                float: left;
                            }
                            .deliveryTime{
                                float: left;
                                // position: absolute;
                                // // top:3rem;
                                // left:2.7rem;
                                margin-left: 0.12rem;
                                font-size: 0.2rem;
                                color:rgb(147,153,159);
                                line-height: 0.24rem;
                                font-weight: 200;
                            }
                        }
                        .text{
                            width: 100%;
                            margin-top:0.12rem;
                            font-size: 0.24rem;
                            color:rgb(7,17,27);
                            line-height: 0.36rem;
                        }
                        .icon-damuzhion{
                            margin-top: 0.16rem;
                            float: left;
                            font-size: 0.24rem;
                            color:rgb(0,160,220);
                            line-height: 0.32rem;
                        }
                        .icon-down{
                            margin-top: 0.16rem;
                            float: left;
                            font-size: 0.24rem;
                            color:rgb(183,187,191);
                            line-height: 0.32rem;
                        }
                        ul{
                            float: left;
                            margin: 0.16rem 0 0 0.16rem;
                            li{
                                margin-right:0.16rem;
                                display: inline-block;
                                border:0.01rem solid rgba(7,17,27,0.1);
                                height: 0.32rem;
                                line-height: 0.32rem;
                                border-radius: 0.02rem;
                                background-color: rgb(255,255,255);
                                padding: 0 0.12rem;
                                font-size: 0.18rem;
                                color:rgb(147,153,159);
                            }
                        }
                    }
                } 
            }
        }
    }
</style>


