<template>
    <div id="shopDetail" v-show="shopFlag">
        <div class="shop">
            <div class="head">
                <p class="car">购物车</p>
                <p class="clear" @click="clear">清空</p>
            </div>
            <div class="list" ref="listDeatil">
                <div >
                    <ul>
                        <li v-for="(item,i) in selectFoods" :key="i">
                            <span class="name">{{item.name}}</span>
                            <span class="price">￥<span class="money">{{item.price}}</span></span>
                            <add :food="item" class="add" v-show="item.count"></add>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex';
    import BScroll from 'better-scroll';
    import Add from '../add/add.vue';
    export default {
        props:[
            'selectFoods'
        ],
        components:{
            add:Add,
        },
         watch:{
            shopFlag(){
                this.$nextTick( () => {
				    setTimeout( () => {
                        if( !this.listBScroll){
                            this.listBScroll = new BScroll(this.$refs.listDeatil,{click:true})
                        }else{
                            this.listBScroll.refresh();
                        }
				    },30);
			    })
            }
        },
        computed:{
            ...mapGetters([
                'shopFlag',
            ]),
        },
        methods:{
            clear(){
                this.selectFoods.forEach(food=>{
                   food.count=0
                });
                this.$router.go('/ele/goods');
            }
        }
        
    }
</script>
<style lang="less" scoped>
    #shopDetail{
        position: absolute;
        width: 100%;
        height: 100%;
        top:0;
        left: 0;
        right: 0;
        bottom:0;
        background-color: rgba(7,17,27,0.6);
        overflow: hidden;
        .shop{
            position: absolute;
            bottom: 0;
            width: 100%;
            height:6.01rem;
            background-color: white;
            .head{
                width: 90.5%;
                height: 0.8rem;
                background-color: #f3f5f7;
                padding: 0 0.36rem;
                display: flex;
                justify-content:space-between;
                line-height: 0.8rem;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                .car{
                    font-size: 0.28rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                }
                .clear{
                    font-size: 0.24rem;
                    color:rgb(0,160,220);
                }
            }
            .list{
                overflow: hidden;
                width: 100%;
                ul{
                    width: 100%;
                    height: 6.3rem;
                    li{
                        height: 0.96rem;
                        margin:0 0.36rem;
                        border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                        position: relative;
                        .name{
                            font-size: 0.28rem;
                            color:rgb(7,17,27);
                            line-height: 0.96rem;
                        }
                        .price{
                            position: absolute;
                            right:1.5rem;
                            top:0.24rem;
                            font-size: 0.2rem;
                            font-weight: normal;
                            color:rgb(240,20,20);
                            line-height: 0.48rem;    
                            .money{
                                font-size: 0.28rem;
                                font-weight:700;
                            }
                        }
                        .add{
                            position: absolute;
                            right:0rem;
                            top:0.24rem;
                        }
                    }
                }
            }
        }
    }
</style>





