<template>
    <div class="addNum">
        <i class="iconfont desc" @click="descNum" v-show="food.count">&#xe631;</i> 
        <span class="count" v-show="food.count">{{food.count}}</span>
        <i class="iconfont add" @click="addNum">&#xe611;</i>
    </div>
</template>
<script>
    import Vue from 'vue'
    import {mapGetters} from 'vuex'
    export default {
        data(){
            return {
                flag:false
            }
        },
        props:[
            'food',
        ],
        methods:{
            addNum(){
                if(!this.food.count){
                    Vue.set(this.food,'count',1);
                }else{
                    this.food.count++;
                }
            },
            descNum(){
               if(this.food.count){
                   this.food.count--;
               }
            }
        }
    }
</script>
<style lang="less" scoped>
    .addNum{
 
        .desc{
            color: rgb(0,160,220);
            font-size: 0.48rem;
            display: inline-block
        }
        .count{
            color: rgb(147,153,159);
            font-size: 0.3rem;
            display: inline-block 
        }
        .add{
            color: rgb(0,160,220);
            font-size: 0.5rem;
            display: inline-block
        }
    }
    
</style>





