<template>
    <div class="header">
        <div class="bg">
            <img :src="seller.avatar">
        </div>
        <div class="bg-gray"></div>
        <div class="top">
            <img :src="seller.avatar" class="img1">
            <div class="right">
                <img src="../../../static/imgs/brand.png" class="img2">
                <p class="name" @click="showDetail">{{seller.name}}</p>
                <p class="time">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
                <div class="decrease">
                    <img src="../../../static/imgs/decrease.png" alt="" class="img3">
                    <span class="description" v-if="seller.supports">{{seller.supports[0].description}}</span>
                    <p class="length" @click="showDetail"><span class="supports" v-if="seller.supports">{{seller.supports.length}}个  > </span></p>
                </div>    
            </div>
        </div>
        <div class="top1" @click="showDetail">
            <img src="../../../static/imgs/bulletin.png" alt="">
            <p class="text">{{seller.bulletin}}</p>
            <p class="bullt">></p>
        </div>
    </div>
</template>

<script>
    import { mapGetters ,mapActions} from 'vuex';
    export default {
        computed:{
           ...mapGetters([
              'seller'
           ])
        },
        methods:{
            ...mapActions([
                'showDetail'
            ])
        }
    }
</script>


<style scoped lang="less">
    .header{
        width: 100%;
        height: 2.68rem;
        overflow: hidden;
        .bg{
            height: 100%; 
            width: 100%; 
            img{
                height: 100%; 
                width: 100%; 
                /* 高斯模糊  blur模糊度，值越大越模糊*/
                filter: blur(0.1rem);
            }    
        }
        .bg-gray{
            position: absolute;
            top:0;
            height: 2.68rem;
            width: 100%;
            background-color: rgba(7,17,27,0.5);
        }
        .top{
            position: absolute;
            top:0;
            width:100%;
            height: 2.12rem;
            .img1{
                width: 1.28rem;
                height: 1.28rem;
                padding: 0.48rem 0.32rem 0.36rem 0.48rem;
                border-radius: 0.04rem;
            } 
            .right{
            padding-left: 2.08rem;
                .img2{
                    width: 0.6rem;
                    height: 0.36rem;
                    position: absolute;
                    top:0.52rem;
                }
                .name{
                    font-size: 0.32rem;
                    color:rgb(255,255,255);
                    font-weight: bold;
                    line-height: 0.36rem;
                    position: absolute;
                    top:0.52rem;
                    left:2.8rem;
                }
                .time{
                    font-size: 0.24rem;
                    color:rgb(255,255,255);
                    font-weight: 2rem;
                    line-height: 0.24rem; 
                    position: absolute;
                    top:1.04rem;
                    left:2.08rem;
                }
                .decrease{
                    position: absolute;
                    top:1.4rem;
                    img{
                        width: 0.32rem;
                        height: 0.32rem;
                    }
                    .description{
                        font-size: 0.2rem;
                        color:rgb(255,255,255);
                        font-weight: 2rem;
                        line-height: 0.24rem;
                        position: relative;
                        left:0.08rem;
                        top:-0.1rem;
                    }
                    .length{
                        width: 0.86rem;
                        height: 0.48rem;
                        font-size: 0.2rem;
                        color:rgb(255,255,255);
                        font-weight: 2rem;
                        line-height: 0.24rem;
                        background-color: rgba(0,0,0,0.2);
                        border-radius: 0.3rem;
                        position: relative;
                        left:4.4rem;
                        top: -0.45rem;
                        .supports{
                            position: relative;
                            left: 22%;
                            top: 22%;
                        }
                    }
                }  
            }   
        }     
        .top1{
            width: 100%;
            height: 0.56rem;
            background-color: rgba(7,17,27,0.2);
            position: absolute;
            top: 2.12rem;
            img{
                width: 0.44rem;
                height: 0.24rem;
                padding: 0.16rem 0.08rem 0.16rem 0.24rem;
            }
            .text{
                position: relative;
                left:0.76rem;
                top:-0.56rem ;
                font-size: 0.2rem;
                font-weight: 200;
                color: rgb(255,255,255);
                line-height: 0.56rem;
                width: 85%;
                overflow: hidden;
                text-overflow:ellipsis;
                white-space:nowrap;
            }
            .bullt{
                color: rgb(255,255,255);
                position: absolute;
                right:0.24rem;
                top:0.16rem;
            }
        }

    }
</style>




