<template>
    <div class="seller" ref="sellerDetail">
        <div>
            <div>
                <div class="head">
                    <p class="name">{{seller.name}}</p>
                    <star :score='seller.score' :size="'middle'" class="star"></star>
                    <div class="count">
                        <span class="ratingCount">({{seller.ratingCount}})</span>
                        <span>月售{{seller.sellCount}}单</span>
                    </div>
                    <div class="xin">
                        <i class="iconfont icon-xin1"></i>
                        <p class="collect">已收藏</p>
                    </div>
                </div>
                <div class="nav">
                    <div class="left">
                        <p class="text">起送价</p>
                        <p class="minPrice">{{seller.minPrice}}<span class="price">元</span></p>
                    </div>
                    <div class="center">
                        <p class="text">商家配送</p>
                        <p class="deliveryPrice">{{seller.deliveryPrice}}<span class="price">元</span></p>
                    </div>
                    <div class="right">
                        <p class="text">平均配送时间</p>
                        <p class="deliveryTime">{{seller.deliveryTime}}<span class="price">分钟</span></p>
                    </div>
                </div>
                <div class="middle"></div>
                <div class="play">
                    <p class="bull">公告与活动</p>
                    <p class="bulletin">{{seller.bulletin}}</p>
                </div>
                <div v-if="seller.supports" class="msg1">
                    <div class="img1"><img src="../../../static/imgs/decrease.png" alt=""></div>
                    <div class="description1">{{seller.supports[0].description}}</div>
                </div>
                <div v-if="seller.supports" class="msg1">
                    <div class="img1"><img src="../../../static/imgs/discount.png" alt=""></div>
                    <div class="description1">{{seller.supports[1].description}}</div>
                </div>
                <div v-if="seller.supports" class="msg1">
                    <div class="img1"><img src="../../../static/imgs/special.png" alt=""></div>
                    <div class="description1">{{seller.supports[2].description}}</div>
                </div>
                <div v-if="seller.supports" class="msg1">
                    <div class="img1"><img src="../../../static/imgs/invoice.png" alt=""></div>
                    <div class="description1">{{seller.supports[3].description}}</div>
                </div>
                <div v-if="seller.supports" class="msg1">
                    <div class="img1"><img src="../../../static/imgs/guarantee.png" alt=""></div>
                    <div class="description1">{{seller.supports[4].description}}</div>
                </div>
                <div class="middle"></div>
                <div class="pictures">
                    <p class="sellerPicture">商家实景</p>
                    <div ref="picture">
                        <ul ref="picList">
                            <li v-for="(item,i) in seller.pics" :key="i">
                                <img :src="item" alt="">
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="middle"></div>
                <div class="info">
                    <p class="sellerInfo">商家信息</p>
                    <ul v-for="(item,k) in seller.infos" :key="k" class="infos">
                        <li class="lists">{{item}}</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import BScroll from 'better-scroll';
    import star from '../star/star.vue'
    import { mapGetters ,mapActions} from 'vuex';
    export default {
        created(){
            this.$nextTick(()=>{
                setTimeout(()=>{
                    this.sellerBs();
                },100)
            });
            
        },
        watch:{
            seller(){
                let picWidth=120;
                let margin=6;
                let width=(picWidth+margin)*this.seller.pics.length-margin;
                this.$refs.picList.style.width=width+"px";
                this.$nextTick(()=>{
                    setTimeout(() => {
                        if( !this.picBScroll){
                            this.picBScroll = new BScroll(this.$refs.picture,{scrollX:true,})
                        }else{
                            this.picBScroll.refresh();
                        }
                    },300)
                })
                // if(!this.picBScroll){
                //     this.picBScroll=new BScroll(this.$refs.picture,{
                //         scrollX:true,
                //     })
                // }else{
                //     this.picBScroll.refresh();
                // }
            }
        },
        components:{
            star
        },
        computed:{
           ...mapGetters([
              'seller'
           ])
        },
        methods:{
            sellerBs(){
                let sellerBScroll=new BScroll(this.$refs.sellerDetail,{
                    click:true
                });
            }
        }
    }
</script>

<style lang="less" scoped>
    .seller{
        position: absolute;
        top: 3.49rem;
        overflow: hidden;
        width:100%;
        bottom: 1rem;
        .head{
            height: 1.2rem;
            width:90%;
            margin:0 0.36rem;
            padding-top: 0.36rem;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            .name{
                font-size: 0.28rem;
                color:rgb(7,17,27);line-height: 0.28rem;
            }
            .star{
                margin:0.16rem 0.16rem 0 0;
                float: left;
            }
            .count{
                padding-top:0.16rem;
                float: left;
                font-size: 0.2rem;
                color:rgb(77,85,93);
                line-height: 0.36rem;
                .ratingCount{
                    margin-right: 0.24rem;
                }
            }
            .xin{
                float: right;
                position: relative;
                top:-0.35rem;
                .icon-xin1{
                    color: rgb(240,20,20);
                    font-size: 0.48rem;
                    line-height: 0.48rem;
                }
                .collect{
                    margin-top: 0.08rem;
                    font-size: 0.2rem;
                    color:rgb(77,85,93);
                    line-height: 0.2rem;
                }
            }
        }
        .nav{
            height: 1.5rem;
            width:100%;
            display: flex;
            justify-content: center;
            .left{
                width: 33%;
                text-align: center;
                margin: 0.36rem 0;
                border-right: 0.01rem solid rgba(7,17,27,0.1);
                .text{
                    font-size: 0.2rem;
                    color:rgb(147,153,159);
                    line-height: 0.2rem;
                }
                .minPrice{
                    margin-top: 0.08rem;
                    font-size: 0.48rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.48rem;
                    .price{
                        font-size: 0.2rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: 0.48rem;
                    }
                }
            }
            .center{
                width: 33%;
                text-align: center;
                margin: 0.36rem 0;
                border-right: 0.01rem solid rgba(7,17,27,0.1);
                .text{
                    font-size: 0.2rem;
                    color:rgb(147,153,159);
                    line-height: 0.2rem;
                }
                .deliveryPrice{
                    margin-top: 0.08rem;
                    font-size: 0.48rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.48rem;
                    .price{
                        font-size: 0.2rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: 0.48rem;
                    }
                }
            }
            .right{
                width: 33%;
                text-align: center;
                margin: 0.36rem 0;
                .text{
                    font-size: 0.2rem;
                    color:rgb(147,153,159);
                    line-height: 0.2rem;
                }
                .deliveryTime{
                    margin-top: 0.08rem;
                    font-size: 0.48rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.48rem;
                    .price{
                        font-size: 0.2rem;
                        font-weight: 200;
                        color:rgb(7,17,27);
                        line-height: 0.48rem;
                    }
                }
            }
        }
        .middle{
            width: 100%;
            height: 0.32rem;
            background-color: #f3f5f7;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            border-top: 0.01rem solid rgba(7,17,27,0.1);
        }
        .play{
            width:90%;
            margin:0 0.36rem;
            .bull{
                font-size: 0.28rem;
                color:rgb(7,17,27);line-height: 0.28rem;
                padding-top: 0.36rem;
            } 
            .bulletin{
                font-size: 0.24rem;
                font-weight: 200;
                color:rgb(240,20,20);
                line-height: 0.48rem;
                padding: 0.16rem 0.24rem 0.32rem 0.24rem;
            }
        }
        .msg1{
            width:90%;
            height: 0.64rem;
            margin:0 0.36rem;
            border-top: 0.01rem solid rgba(7,17,27,0.1);
            padding:0.32rem 0.24rem;
            display: flex;
            .img1{
                width: 0.32rem;
                height: 0.32rem;
                margin-right: 0.24rem;
            }
            .description1{
                margin-left: 0.12rem;
                font-size: 0.24rem;
                font-weight: 200;
                color:rgb(7,17,27);
                line-height: 0.64rem;
            }
        }
        .pictures{
            margin-left:0.36rem;
            .sellerPicture{
                font-size: 0.28rem;
                color:rgb(7,17,27);line-height: 0.28rem;
                padding-top: 0.36rem;
            }
            div{
                ul{
                white-space: nowrap;
                    li{
                        display: inline-block;
                        img{
                            width: 2.4rem;
                            height: 1.8rem;
                            margin:0.24rem 0.12rem 0.36rem 0;
                        }
                        &:last-child{
                            margin:0
                        }
                    }
                }
            }    
        }
        .info{
            width:100%;
            padding:0 0 0.24rem 0.36rem;
            .sellerInfo{
                font-size: 0.28rem;
                color:rgb(7,17,27);line-height: 0.28rem;
                padding: 0.36rem 0 0.24rem 0;
            }
            .infos{
                width:85%;
                border-top: 0.01rem solid rgba(7,17,27,0.1);
                display: flex;
                padding:0.32rem 0;
                .lists{
                    font-size: 0.24rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.32rem;
                    padding:0 0.24rem;
                }
            }
        }
        
    }
</style>


