import types from '../types.js';
import axios from 'axios';

var state={
    goods:[],
    food:{},
    goodFlag:false,
    shopFlag:false,
    selectTypes:2,
    selectRatingss:[],
    onlyContents:false
};
var getters={
    goods(state){
        return state.goods
    },
    food(state){
        return state.food
    },
    goodFlag(state){
        return state.goodFlag
    },
    shopFlag(state){
        return state.shopFlag
    },
    selectRatingss(state){
        return state.selectRatingss
    },
    selectTypes(state){
        return state.selectTypes
    },
    onlyContents(state){
        return state.onlyContents
    }
};
var actions={
    getGoods({commit,state}){
        axios.get('./data.json')
             .then((res)=>{
                // if( res.data.errno === 0 ){
                    commit( types.GET_GOODS , res.data.goods );
                // }
             })
    },
    showGoodsDetail({commit,state},m){
        commit(types.SHOWGOODDETAIL,m);
    },
    hideFlag({commit}){
        commit(types.HIDEGOODDETAIL);
    },
    shopShow({commit}){
        commit(types.SHOWSHOPDETAIL);
    },
    Select({commit},n){
        commit(types.SELECTS,n);
    },
    Toggle({commit}){
        commit(types.ONLYCONTENTS);
    }

};
var mutations={
    [types.GET_GOODS](state,data){
        state.goods=data
    },
    [types.SHOWGOODDETAIL](state,m){
        state.goodFlag=true;
        state.food=m
        state.selectRatingss = state.food.ratings;
    },
    [types.HIDEGOODDETAIL](state){
        state.goodFlag=false;
    },
    [types.SHOWSHOPDETAIL](state){
        state.shopFlag=!state.shopFlag;
    },
    [types.SELECTS](state,n){
        state.selectTypes=n;
        state.selectRatingss=state.food.ratings.filter(rating=>{
            if(state.onlyContents && !rating.text){
                return false;
            }else if(state.selectTypes===2){
                return true;
            }else{
                return rating.rateType==state.selectTypes;
            }
        })
    },
    [types.ONLYCONTENTS](state){
        state.onlyContents=!state.onlyContents;
        state.selectRatingss=state.food.ratings.filter(rating=>{
            if(state.onlyContents && !rating.text){
                return false;
            }else if(state.selectTypes===2){
                return true;
            }else{
                return rating.rateType==state.selectTypes;
            }
        })
    }
};
export default({
    state,
    getters,
    actions,
    mutations
})


