import types from '../types.js';
import axios from 'axios';

var state={
    ratings:[],
    selectRatings:[],
    selectType:2,
    onlyContent:false
};
var getters={
    ratings(state){
        return state.ratings
    },
    selectRatings(state){
        return state.selectRatings
    },
    selectType(state){
        return state.selectType
    },
    onlyContent(state){
        return state.onlyContent
    }
};
var actions={
    showRatings({commit,state}){
        axios.get('./data.json')
             .then((res)=>{
            //  if( res.data.errno === 0 ){
                commit( types.GET_RATINGS , res.data.ratings );
                // }
             })
    },
    //执行选择的是全部／满意／不满意
    select({commit},n){
        commit(types.SELECT,n);
    },
    toggle({commit}){
        commit(types.ONLYCONTENT);
    }
};
var mutations={
    [types.GET_RATINGS](state,data){
        state.ratings=data;
        state.selectRatings = data;
    },
    [types.SELECT](state,n){
        state.selectType=n;
        state.selectRatings=state.ratings.filter(rating=>{
            if(state.onlyContent && !rating.text){
                return false;
            }else if(state.selectType===2){
                return true;
            }else{
                return rating.rateType==state.selectType;
            }
        })
    },
    [types.ONLYCONTENT](state){
        state.onlyContent=!state.onlyContent;
        state.selectRatings=state.ratings.filter(rating=>{
            if(state.onlyContent && !rating.text){
                return false;
            }else if(state.selectType===2){
                return true;
            }else{
                return rating.rateType==state.selectType;
            }
        })
    }
};
export default({
    state,
    getters,
    actions,
    mutations
})


