<template>
    <div class="goods">
        <div class="menu-wrap" ref="menu">
            <ul>
                <li v-for="(item,i) in goods" @click="selectMenu(i)" :key="i" :class="{'current' : i===currentIndex}">
                    <span>{{item.name}}</span>
                </li>
            </ul>
        </div>
        <div class="foods-wrap" ref="foods">
            <div>
                <div v-for="(item,key) in goods" :key="key" class="food-list-hook">
                    <div class="name">{{item.name}}</div>
                    <div v-for="(m,k) in item.foods" :key="k" class="list">
                        <img :src="m.icon" alt="" class="icon" @click="showGoodsDetail(m)">
                        <div class="right">
                            <p class="listName">{{m.name}}</p>
                            <p class="description" v-if="m.description">{{m.description}}</p>
                            <p class="description">月售{{m.sellCount}}份&nbsp;&nbsp;&nbsp;好评率{{m.rating}}％</p>
                            <p class="price">
                                <span class="newPrice">¥{{m.price}}</span>
                                <span v-show="m.oldPrice" class="oldPrice">¥{{m.oldPrice}} </span>   
                            </p>
                            <add :food="m" class="add"></add>   
                        </div>
                    </div>
                </div>
            </div> 
        </div>
    </div>
</template>

<script>
    import { mapGetters,mapActions } from 'vuex';
    import BScroll from 'better-scroll';
    import Add from '../add/add.vue';
    export default {
        data:function(){
            return {
                listHeight:[],
                scrollY:0
            }
        },
        components:{
            add:Add,
        },
        watch:{
            goods(){
                this.$nextTick(()=>{
                    this.menuBs();
                    this.calcHeight()
                })
            }
        },
        computed:{
            ...mapGetters([
                'goods'
            ]),
            selectFoods(){
                let foods=[];
                this.goods.forEach(good=>{
                   good.foods.forEach(food=>{
                       if(food.count>0){
                           foods.push(food);
                       }
                   })
                });
                return foods;
            },
            //计算当前索引
            currentIndex(){
               for(let i=0;i<this.listHeight.length;i++){
                  let height1=this.listHeight[i];
                  let height2=this.listHeight[i+1];
                  //根据滚动的y轴坐标值与listHeight数组区间进行比较
                //   if(this.scrollY>=height1 && this.scrollY<height2){
                //       return i;
                //   }
                    if(this.scrollY<this.listHeight[i+1]-1){
                        return i
                    }
               }
               return 0;
            }
        },
        created(){
            this.$store.dispatch('getGoods');
        },
        methods:{
            showGoodsDetail(m){
                this.$store.dispatch('showGoodsDetail',m);
            },
            menuBs(){
                let menuBScroll=new BScroll(this.$refs.menu,{
                    click:true
                });
                this.foodsBScroll=new BScroll(this.$refs.foods,{
                    click:true,
                    probeType:3
                });
                //获取y轴坐标
                this.foodsBScroll.on('scroll',(pos)=>{
                    this.scrollY=Math.abs(Math.round(pos.y))
                })
            },
            // 计算li的高度 并且push进listHeight数组当中
           calcHeight(){
            //    获取所有的li
               let foodList=this.$refs.foods.getElementsByClassName('food-list-hook');
            //    刚开始初始值为0
               let height=0;
            //    将初始值push到listHeight数组中
               this.listHeight.push(height);
            //    循环所有li
               for(let i=0;i<foodList.length;i++){
                    height +=foodList[i].clientHeight;
                    this.listHeight.push(height);
               }
           },
        //    左侧点击事件
            selectMenu(i){
                let foodList=this.$refs.foods.getElementsByClassName('food-list-hook');
                let el=foodList[i];
                this.foodsBScroll.scrollToElement(el,300);
            }
        }
    }
</script>

<style lang="less" scoped>
    .goods{
        position: absolute;
        top: 3.49rem;
        display: flex;
        overflow: hidden;
        width:100%;
        bottom: 1rem;
        .menu-wrap{
            flex:0 0 1.6rem;
            width:1.6rem;
            background-color: #f3f5f7;

            li{
                height: 1.08rem;
                width: 1.12rem;
                font-weight: 2rem;
                font-size: 0.24rem;
                color:rgb(77,85,93);
                display: table;
                text-align: left;
                padding: 0 0.24rem;
                &.current{
                    background: white;
                }
                span{
                    display: table-cell;
                    vertical-align: middle;
                    border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                }
            }
            li:nth-child(2){
                background-image: url(./special.png);
                background-size:0.3rem 0.3rem;
                background-repeat: no-repeat;
                background-position: 0.22rem 0.22rem;
                span{
                    text-indent: 0.3rem;
                }
            }
        }
        .foods-wrap{
            flex:1;
            .name{
                width: 100%;
                height: 0.52rem;
                border-left:0.05rem solid #d9dde1;
                background-color: #f3f5f7;
                line-height: 0.52rem;
                color: rgb(147,153,159);
                font-size: 0.24rem;
                padding-left: 0.28rem;
            }
            .list{
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                display: flex;
                padding: 0.36rem 0.36rem 0.2rem 0.36rem;
                .icon{
                    width: 1.14rem;
                    height: 1.14rem;
                    float: left;
                }
                .right{
                    width: 3.8rem;
                    float: left;
                    padding-left: 0.2rem;
                    position: relative;
                    .listName{
                       padding-top: 0.04rem;
                       font-size: 0.28rem;
                       color:rgb(7,17,27);
                       line-height: 0.28rem;
                    }
                    .description{
                        font-size: 0.2rem;
                        color:rgb(147,153,159);
                        line-height: 0.2rem;
                        margin-top: 0.16rem;
                    }
                    .price{
                        float: left;
                        margin-top: 0.16rem;
                        font-size: 0.28rem;
                        font-weight: 700;
                        position: relative;
                        .newPrice{
                            color: rgb(240,20,20);
                        }
                        .oldPrice{
                            position: absolute;
                            top: 0.05rem;
                            left: 0.6rem;
                            float: left;
                            font-size: 0.2rem;
                            color: rgb(147,153,159);
                            text-decoration: line-through;
                        }  
                    }
                    .add{
                        float: right;
                    }
                }    
            }
        }
    }
</style>





