import types from '../types.js';
import axios from 'axios';

var state={
    seller:{},
    showFlag:false
};
var getters={
    seller(state){
       return state.seller
    },
    showFlag(state){
        return state.showFlag
    }
};
var actions={
    getSeller({commit,state}){
        axios.get('./data.json')
             .then((res)=>{
                // if(res.data.errno===0){
                    commit(types.GET_SELLER,res.data.seller);
                // }
             })
    },
    showDetail({commit}){
        commit(types.SHOWDETAIL);
    },
    hideDetail({commit}){
        commit(types.HIDEDETAIL);
    }
};
var mutations={
    [types.GET_SELLER](state,data){
        state.seller=data
    },
    [types.SHOWDETAIL](state){
        state.showFlag=true
    },
    [types.HIDEDETAIL](state){
        state.showFlag=false
    }
};
export default({
    state,
    getters,
    actions,
    mutations
})


