<template>
    <div class="bottom">
        <div class="shopCar" @click="shopShow" :class="{'totalCar1':totalCount!==0}">
            <i class="iconfont car" :class="{'totalCar':totalCount!==0}">&#xf0082;</i>
            <p v-show="totalCount" class="count">{{totalCount}}</p>
        </div>
        <div class="center">
            <p class="total">¥{{totalPrice}}</p>
            <p class="price">另需配送费4元</p>
        </div>
        <div class="right" :class="{'current': pay=='去结算'}">
            <p class="atLeast" @click="payPrice()" :class="{'current1': pay=='去结算'}">{{pay}}</p>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    export default {
        props:[
            'goods'
        ],
        computed:{
           ...mapGetters([
               'seller'
           ]),
            selectFoods(){
                let foods=[];
                this.goods.forEach(good=>{
                   good.foods.forEach(food=>{
                       if(food.count>0){
                           foods.push(food);
                       }
                   })
                });
                return foods;
            },
            totalCount(){
                let count=0;
                this.selectFoods.forEach(food=>{
                    count+=food.count
                });
                return count;
            },
            totalPrice(){
                let price=0;
                this.selectFoods.forEach(food=>{
                    price+=food.price*food.count
                });
                return price;
            },
            pay(){
                if(this.totalPrice===0){
                    return `¥ ${this.seller.minPrice}元起送`
                }else if(this.totalPrice<this.seller.minPrice){
                    return `还差¥ ${this.seller.minPrice-this.totalPrice}元起送`
                }else{
                    return "去结算"
                }
            }
        },
        methods:{
            shopShow(){
                if(this.totalCount!==0){
                    this.$store.dispatch('shopShow');
                }
                
            },
            // payPrice(){
            //     if(this.pay=="去结算"){
            //         // alert("您共需支付"+Number(this.totalPrice)+4+"元");
            //     }
            // }
        }
    }
</script>
<style lang="less" scoped>
    .bottom{
        width: 100%;
        height: 1rem;
        background-color: #141d27;
        position: absolute;
        bottom:0;
        .shopCar{
            position: relative;
            left: 0.24rem;
            top: -0.2rem;
            width: 0.88rem;
            height: 0.88rem;
            border: 0.12rem solid #141d27;
            border-radius: 50%;
            background-color: #414548;
            &.totalCar1{
                background-color: rgb(0,160,220);
            }
            .car{
                position: relative;
                left: 0.17rem;
                top: 0.15rem;
                color: rgba(255,255,255,0.4);
                &.totalCar{
                    color:rgb(255,255,255);
                }
            }
            .count{
                position: relative;
                left:0.55rem;
                top:-0.6rem;
                border-radius: 0.25rem;
                width: 0.48rem;
                height: 0.32rem;
                font-size: 0.2rem;
                text-align: center;
                color: rgb(255,255,255);
                line-height: 0.32rem;
                background-color: rgb(240,20,20);
                box-shadow: 0rem 0.04rem 0.08rem 0 rgba(0,0,0,0.4);
            }
        }
        .center{
                position: relative;
                top:-1rem;
                height: 0.32rem;;
                margin-left: 1.7rem;
                line-height: 0.7rem;
                font-size: 0.32rem;
                color: rgba(255,255,255,0.4);
                font-weight: 700;
                .total{
                    width: 0.8rem;
                    float: left;
                    font-size: 0.38rem;
                    padding-right: 0.24rem;
                    border-right: 0.01rem solid rgba(255,255,255,0.4);
                }
                .price{
                    float: left;
                    font-size: 0.28rem;
                    padding-left: 0.24rem;
                }
            }
        .right{
            position: relative;
            top:-1.44rem;
            left:5.4rem;
            width: 2.1rem;
            height: 100%;
            background-color: rgb(43,51,59);
            .atLeast{
                font-size: 0.24rem;
                color: rgba(255,255,255,0.4);
                font-weight: 700;
                text-align: center;
                &.current1{
                    color:rgb(255,255,255);
                }
            }
            &.current{
                background-color: rgb(0,160,220);
            }    
        }
    }
</style>





