<template>
    <div id="goodDetail" v-show="goodFlag" ref="good">
        <div>
            <div>
            <img :src="food.image" alt="" class="foodImg">
            <p class="back" @click="hideFlag"><</p>
            <div class="detail">
                <P class="name">{{food.name}}</P>
                <p class="description">月售{{food.sellCount}}份&nbsp;&nbsp;&nbsp;好评率{{food.rating}}％</p>
                <p class="price">
                    <span class="newPrice">¥{{food.price}}</span>
                    <span v-show="food.oldPrice" class="oldPrice">¥{{food.oldPrice}} </span>
                </p>
                <p class="shop" v-show="!food.count" @click="addFirst">加入购物车</p>
                <add :food="food" class="add" v-show="food.count"></add>
            </div>
            <div class="middle"></div>
            <div class="introduce">
                <p class="goodsIntroduce">商品介绍</p>
                <p class="info">{{food.info}}</p>
            </div>
            <div class="middle"></div>
            <div class="rating">
                <p class="goodsRating">商品评价</p>
                <div class="list">
                    <p class="total" @click="select(2)">全部&nbsp;&nbsp;<span class="num" v-if="food.ratings">{{food.ratings.length}}</span></p>
                    <p class="Recommend" @click="select(0)">推荐&nbsp;&nbsp;<span class="num"  v-if="food.ratings">{{goodCount}}</span></p>
                    <p class="complaints" @click="select(1)">吐槽&nbsp;&nbsp;<span class="num" v-if="food.ratings">{{food.ratings.length-goodCount}}</span></p>
                </div>
                <div class="content">
                    <i class="iconfont yes" @click="toggle" :class="{'toggle' : onlyContents!==false}">&#xe608;</i>
                    <span class="watchContent">只看有内容的评价</span>
                </div>
                <div v-for="(item,i) in selectRatingss" :key="i" class="ratings">
                    <div class="user">
                        <p class="rateTime">{{item.rateTime | change}}</p>
                        <p class="username">{{item.username}}</p>
                        <img :src="item.avatar" alt="" class="headImg">
                    </div>
                    <div class="texts">
                        <i class="iconfont" :class="item.rateType === 0 ? 'icon-damuzhion':'icon-down'"></i>
                        <span class="text">{{item.text}}</span>
                    </div>
                </div>  
            </div>
        </div>
        </div>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex';
    import BScroll from 'better-scroll';
    import Add from '../add/add.vue';
    import Vue from 'vue'
    export default {
         components:{
            add:Add
        },
        watch:{
            food(){
                this.$nextTick(()=>{
                    setTimeout( () => {
                        if( !this.goodBScroll){
                            this.goodBScroll = new BScroll(this.$refs.good,{click:true})
                        }else{
                            this.goodBScroll.refresh();
                        }
                        // this.goodBs();
                    },300)
                })
            }
        },
        filters:{
            change(value){
                let date=new Date(value);
                let Y=date.getFullYear()+'-';
                let M=(date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
                let D=date.getDate() + ' ';
                let h = date.getHours() + ':';
                let m = date.getMinutes() + ':';
                let s = date.getSeconds(); 
                return value=Y+M+D+h+m+s ;                                                                                                                                             
            }
        },
        computed:{
            ...mapGetters([
                'goodFlag',
                'food',
                'ratings',
                'selectRatingss',
                'onlyContents'
            ]),
            goodCount(){
                return this.food.ratings.filter(rating=>{
                    return rating.rateType===0;
                }).length;
            },
        },
        methods:{
            goodBs(){
                let goodBScroll=new BScroll(this.$refs.good,{
                    click:true
                });
            },
            hideFlag(){
                this.$store.dispatch('hideFlag');
            },
            addFirst(){
                if ( !this.food.count ){
                    Vue.set(this.food,'count',1);
                }
            },
            select(n){
                this.$store.dispatch('Select',n);
            },
            toggle(){
                this.$store.dispatch('Toggle');
            }
        }
    }
</script>
<style lang="less" scoped>
    #goodDetail{
        overflow: hidden;
        position: absolute;
        width: 100%;
        top:0;
        left: 0;
        right: 0;
        bottom:0;
        background-color: white;
        .foodImg{
            width: 100%;
            height: 7.5rem;
        }
        .back{
            position: absolute;
            left: 0.36rem;
            top: 0.36rem;
            color:white;
            font-size: 0.48rem;
        }
        .detail{
            position: relative;
            width: 100%;
            height: 2.19rem;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            .name{
                font-size: 0.28rem;
                font-weight: 700;
                color:rgb(7,17,27);
                line-height: 0.28rem;
                padding:0.36rem 0 0.16rem 0.36rem;
            }
            .description{
                font-size: 0.2rem;
                color: rgb(147,153,159);
                line-height: 0.2rem;
                padding:0 0 0.36rem 0.36rem;
            }
            .price{
                padding:0 0 0.48rem 0.36rem;
                width: 2rem;
                .newPrice{

                    font-size: 0.28rem;
                    font-weight: 700;
                    color: rgb(240,20,20);
                    line-height: 0.48rem;
                }
                .oldPrice{
                    font-size: 0.2rem;
                    color: rgb(147,153,159);
                    text-decoration: line-through;
                }
            }
            .shop{
                position: absolute;
                right: 0.36rem;
                bottom:0.36rem;
                width: 1.6rem;
                height: 0.48rem;
                background-color:rgb(0,160,220); 
                color:rgb(255,255,255);
                font-size: 0.2rem;
                line-height: 0.48rem;
                border-radius: 0.24rem;
                text-align: center;
            }
            .add{
                position: absolute;
                right: 0.36rem;
                bottom:0.36rem;
                border-radius: 0.24rem;
                
            }
        }
        .middle{
            width: 100%;
            height: 0.32rem;
            background-color: #f3f5f7;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
        }
        .introduce{
            width: 100%;
            height: 2.53rem;
            .goodsIntroduce{
                font-size: 0.28rem;
                color:rgb(7,17,27);
                line-height: 0.28rem;
                padding:0.36rem 0 0.16rem 0.36rem;
            }
            .info{
                font-size: 0.24rem;
                font-weight: 200;
                color:rgb(77,85,93);
                line-height: 0.48rem;
                padding: 0 0.52rem 0.36rem 0.52rem;
            }
        }
        .rating{
            width: 100%;
            .goodsRating{
                font-size: 0.28rem;
                color:rgb(7,17,27);
                line-height: 0.28rem;
                padding:0.36rem 0 0.36rem 0.36rem;
            }
            .list{
                margin:0 0.36rem 0 0.36rem;
                padding-bottom: 0.36rem;
                display: flex;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                .total{ 
                    width: 1.19rem;
                    height: 0.64rem;
                    background-color: rgb(0,160,220);
                    color:rgb(255,255,255);
                    font-size: 0.24rem;
                    line-height: 0.64rem;
                    padding-left: 0.24rem;
                    border-radius:0.02rem;
                    .num{
                        font-size: 0.24rem;
                    }
                }
                .Recommend{
                    width: 1.19rem;
                    height: 0.64rem;
                    background-color: rgba(0,160,220,0.2);
                    border-radius:0.02rem;
                    margin-left: 0.16rem;
                    padding-left: 0.24rem;
                    font-size: 0.24rem;
                    color:rgb(77,85,93);
                    line-height: 0.64rem;
                }
                .complaints{
                    width: 1.19rem;
                    height: 0.64rem;
                    background-color: rgba(77,85,93,0.2);
                    border-radius:0.02rem;
                    margin-left: 0.16rem;
                    padding-left: 0.24rem;
                    font-size: 0.24rem;
                    color:rgb(77,85,93);
                    line-height: 0.64rem;
                }
            }
            .content{
                width: 100%;
                height: 1rem;
                margin:0.24rem 0;
                padding-left: 0.36rem;
                border-bottom:0.01rem solid rgba(7,17,27,0.1);
                .yes{
                    color:rgb(147,153,159);
                    // line-height: 0.48rem;
                    &.toggle{
                        color:rgb(0,160,220);
                    }
                }
                .watchContent{
                    padding-left: 0.08rem;
                    font-size: 0.24rem;
                    color:rgb(147,153,159);
                    // line-height: 0.48rem;
                }
            }
            .ratings{
                margin: 0.24rem 0.36rem 0.32rem 0.36rem;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                .user{
                    font-size: 0.2rem;
                    color:rgb(147,153,159);
                    line-height: 0.24rem;
                    display: flex;
                    justify-content:space-between;
                    .rateTime{
                        float: left;
                    }
                    .username{
                        float: left;
                        position: relative;
                        left:1.8rem;
                    }
                    .headImg{
                        margin-left: 0.12rem;
                        float: left;
                        width: 0.24rem;
                        height: 0.24rem;
                        border-radius: 50%;
                    }
                }
                .texts{
                    line-height: 0.32rem;
                    margin: 0.12rem 0 0.32rem 0;
                    .icon-damuzhion{
                       font-size: 0.24rem;
                       color:rgb(0,160,220);
                    }
                    .icon-down{
                        font-size: 0.24rem;
                        color:rgb(147,153,159);
                    }
                    .text{
                        padding-left: 0.08rem;
                        font-size: 0.24rem;
                        color:rgb(7,17,27);
                        line-height: 0.32rem;
                    }

                }
            }
        }

    }
</style>





