<template>
    <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
        <div id="detail" v-show="showFlag">
            <p @click="hideDetail" class="name">{{seller.name}}</p> 
            <star :score='seller.score' :size="'big'" class="star"></star> 
            <div class="title">
                <div class="line"></div>
                <div class="text">优惠信息</div>
                <div class="line"></div>
            </div>
            <div v-if="seller.supports" class="msg1">
                <div class="img1"><img src="../../../static/imgs/decrease.png" alt=""></div>
                <div class="description1">{{seller.supports[0].description}}</div>
            </div>
            <div v-if="seller.supports" class="msg1">
                <div class="img2"><img src="../../../static/imgs/discount.png" alt=""></div>
                <div class="description1">{{seller.supports[1].description}}</div>
            </div>
            <div v-if="seller.supports" class="msg1">
                <div class="img2"><img src="../../../static/imgs/special.png" alt=""></div>
                <div class="description1">{{seller.supports[2].description}}</div>
            </div>
            <div v-if="seller.supports" class="msg1">
                <div class="img2"><img src="../../../static/imgs/invoice.png" alt=""></div>
                <div class="description1">{{seller.supports[3].description}}</div>
            </div>
            <div v-if="seller.supports" class="msg1">
                <div class="img2"><img src="../../../static/imgs/guarantee.png" alt=""></div>
                <div class="description1">{{seller.supports[4].description}}</div>
            </div>
            <div class="title">
                <div class="line"></div>
                <div class="text">商家公告</div>
                <div class="line"></div>
            </div>
            <div class="bulletin">
                <p class="content">{{seller.bulletin}}</p>
            </div>
            <div class="close" @click="hideDetail">
                <i class="iconfont off">&#xe648;</i>
            </div>
        </div>
    </transition>
</template>

<script>
    import { mapGetters } from 'vuex';
    import animate from 'animate.css';
    import star from '../star/star.vue'

    export default {
        props:["name"],
        components:{
            star
        },
        computed:{
            ...mapGetters([
                'seller',
                'showFlag'
            ])
        },
        methods:{
            hideDetail(){
                this.$store.dispatch('hideDetail');
            }
        }
    }
</script>

<style lang="less" scoped>
    #detail{
        overflow: hidden;
        position: absolute;
        width: 100%;
        height: 100%;
        top:0;
        left: 0;
        right: 0;
        bottom:0;
        background-color: rgba(7,17,27,0.85);
        .name{
            font-size: 0.32rem;
            font-weight: 7rem;
            color: rgb(255,255,255);
            line-height: 0.32rem;
            padding-top: 1.28rem;
            text-align: center;
        }
        .star{
            margin-top: 0.32rem;
            text-align: center;
        }
        .title{
            width: 80%;
            display: flex;
            margin:0.56rem auto 0 auto;
            .line{
                flex: 1;
                position :relative;
                top :-0.14rem;
                border-bottom: 0.02rem solid rgba(255, 255, 255, 0.2);
            } 
            .text{
                padding: 0 0.24rem;
                font-size :0.28rem;
                font-weight: 7rem;
                color: rgb(255,255,255);
            }
            
        }
        .msg1{
            margin-top:0.3rem;
            margin-left:0.96rem;
            .img1{
                width: 0.34rem;
                height: 0.34rem;
            }
            .img2{
                width: 0.30rem;
                height: 0.30rem;
            }
            .description1{
                font-size: 0.24rem;
                font-weight: 2rem;
                color:rgb(255,255,255);
                line-height: 0.28rem;
                position: relative;
                left:0.8rem;
                top:-0.1rem;
            }
        }
        .bulletin{
            width: 80%;
            // height: 200px;
            margin: 0.48rem auto;
            .content{
                padding: 0 0.24rem;
                font-weight: 2rem;
                color: rgb(255,255,255);
                line-height: 0.48rem;
                font-size: 0.24rem;
            }  
        }
        .close{
            .off{
                font-size: 0.5rem;
                color:rgba(255,255,255,0.5);
                position: relative;
                left:47%;
            }
        }
          
          
    }
    
</style>




