<template>
  <div id="app">
    <v-header></v-header>
    <div class="tab">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评价</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <goodDetail></goodDetail>
    <shopDetail :selectFoods='selectFoods'></shopDetail>
    <bottom :goods="goods"></bottom>
    <detail></detail>
  </div>
</template>

<script>
import header from './components/header/header.vue'
import detail from './components/detail/detail.vue'
import bottom from './components/bottom/bottom.vue'
import goodDetail from './components/goodDetail/goodDetail.vue'
import shopDetail from './components/shopDetail/shopDetail.vue'
import {mapGetters} from 'vuex'
export default {
  components:{
    'v-header':header,
    detail,
    bottom,
    goodDetail,
    shopDetail

  },
  computed:{
    ...mapGetters([
        'goods',

    ]),
    selectFoods(){
      let foods=[];
      this.goods.forEach(good=>{
        good.foods.forEach(food=>{
          if(food.count>0){
              foods.push(food);
            }
          })
        });
        return foods;
    },
  },
  created(){
    this.$store.dispatch('getSeller');
  }
}
</script>

<style lang="less" scoped>
/* #app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
} */
  .tab{
    width: 100%;
    height: 0.8rem;
    position: absolute;
    top: 2.68rem;
    font-size: 0.28rem;
    color: rgb(77,85,93);
    line-height: 0.8rem;
    border-bottom:0.01rem solid rgba(7,17,27,0.1);
    display: flex;
    .tab-item{
      flex:1;
      text-align: center;
      a{
        display: block;
        &.router-link-active{
         color:rgb(240,20,20)
        }
      }
    }
  }
</style>



